<template>

<div>

  <div class="table">
    <el-table
    :data='videoID_list'
    class="tb-edit"
    style="width: 100%"
    >
      <el-table-column label="VideoID"  width="200" align="center" >
            <template v-slot="scope0">
                <el-input v-model="videoID_list[scope0.$index]" placeholder="videoID input" ></el-input>     
            </template>
      </el-table-column>

      <el-table-column  label="SegInfo"  width="400" align="center" >
            <template v-slot="scope_1">
                <el-input v-model="SegInfo_list[scope_1.$index]" placeholder="Please end with ;" ></el-input>     
            </template>
      </el-table-column>

      <el-table-column label="Operation" align="center" width="180">
            <template v-slot="scope_2">
                <el-button
                        size="mini"
                        @click="AddVideo(scope_2.$index, scope_2.row)">ADD</el-button>
                <el-button
                        size="mini"
                        type="danger"
                        @click="DeleteVideo(scope_2.$index, scope_2.row)">DELETE</el-button>
            </template>
        </el-table-column>

    </el-table>

  </div>

<div class = 'upload' style="width: 60%">
  <el-button @click="generateSeg" >Initialization</el-button>
</div>
 
  <!-- <div class = "urlInput" style="width: 60%">
    <el-input v-model="videoUrl" placeholder="Please enter video ID|Sample input:L77ujOmwe1E"></el-input>
  </div>

  <div class = "urlInput" style="width: 60%">
    <el-input v-model="SegInfo" placeholder="Sample input:0:00-0:30;0:30-1:52; or 0000-0030;0030-0152;"></el-input>
    <el-button @click="generateSeg" >Initialization</el-button>
  </div> -->

  <div class="table">
    <!-- <el-button @click="change"></el-button> -->
    <el-table
            :data="tableData"
            class="tb-edit"
            style="width: 100%"
            :row-class-name="tableRowClassName"
            >

        <el-table-column prop="Index" label="index" width="60" align="center"> </el-table-column>

        <el-table-column  v-for="(item,index) in tableHead1" :key="index" :label="item.label" :property="item.property" width="600" align="center" >
            <template v-slot="scope">
                <el-input v-model="scope.row[scope.column.property]" ></el-input>     
            </template>
        </el-table-column>

        <!-- <el-table-column  :label="timeHead.label" :property="timeHead.property" width="200" align="center" >
            <template v-slot="scope">
                <el-input v-model="timeValue[scope.$index]" placeholder="Please end with ;"></el-input>     
            </template>
        </el-table-column> -->
        <el-table-column  v-for="(item,index) in videoUrl_list" :key="index" :label="item" width="200" align="center"  >
            <template v-slot="scope_3">
                <el-input v-model="timeValue_multiVideo[scope_3.$index][item]" placeholder="Please end with ;"></el-input>     
            </template>
        </el-table-column>




        <!-- <el-table-column :label="MetaTypeTitle" width="180">
          <template v-slot="scope1">
                <el-select v-model="scope1.row[scope1.column.property]" placeholder="Please Select MetaType" @change="changeMetaType($event,scope1.$index)">
                    <el-option
                      v-for="item in MetaType"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                </el-select>
          </template>    
        </el-table-column> -->

        <el-table-column :label="MetaTypeTitle" width="120" align="center" >
          <template v-slot="scope1">
                <el-select v-model="TypeValue[scope1.$index]" placeholder="Select MetaType" @change="changeMetaType($event,scope1.$index)" >
                    <el-option
                      v-for="item in MetaType"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                </el-select>
          </template>    
        </el-table-column>

        <!-- <el-table-column :label="SegmentTitle" width="180" align="center" >
            <template v-slot="scope2">
                <el-select v-model="SegValue[scope2.$index]" placeholder="Select Segment" @change="changeSegValue($event,scope2.$index)">
                    <el-option
                      v-for="item in Segments"
                      :key="item"
                      :label="item"
                      :value="item">
                    </el-option>
                </el-select>
            </template>    
        </el-table-column> -->

        <!-- <el-table-column :label="SegmentTitle" width="450" align="center" >
            <template v-slot="scope2">              
                    <el-checkbox v-for="seg in Segments"  :label="seg" :key="seg" @change="changeSegValue($event,scope2.$index,seg)">{{seg}}</el-checkbox>
            </template>    
        </el-table-column> -->
        
         <el-table-column label="ImgUpload" width="150" align="center">
              <template v-slot="scope3">
                  <el-upload  action="" accept="image/jpeg,image/png"
                    :on-remove="removeImg"
                    :on-change="onUploadChange"
                    :auto-upload="false"
                    :show-file-list="true"
                    >
                      <el-button size="small" type="primary" @click="setrow(scope3.$index)">upload img</el-button>
                  </el-upload>
              </template>
          </el-table-column>

          <!-- <el-table-column label="Bbox" width="150">
              <template v-slot="scope4">
                  <el-upload  action="" accept="image/jpeg,image/png"
                    :on-remove="removeImg1"
                    :on-change="onUploadChange1"
                    :auto-upload="false"
                    :show-file-list="true"
                    >
                      <el-button size="small" type="primary" @click="setrow1(scope4.$index)">upload img</el-button>
                  </el-upload>
              </template>
          </el-table-column> -->

        <el-table-column label="Operation" align="center" width="270">
            <template v-slot="scope">
                <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">ADD</el-button>
                <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">DELETE</el-button>
                <el-button
                        size="mini"
                        @click="Rephrase(scope.$index, scope.row)">Rephrase</el-button>
            </template>
        </el-table-column>

    </el-table>
  </div>

  <div class = 'upload'>
    <el-button @click="upload">Upload</el-button>
    <el-button @click="uploadImg">Save Images</el-button>
    <el-button @click="uploadOriginalInfo">Upload Original Info</el-button>
    
  </div>

  <div class = 'upload'>
      <el-upload  action="" 
                    :on-remove="JsonRemove"
                    :on-change="JsonChange"
                    :auto-upload="false"
                    :show-file-list="true"
                    >
                    <el-button @click="jsonunfold()">Unfold json</el-button>
         </el-upload>  
  </div>
  

</div>


</template>

<script>

import 'element-plus/dist/index.css'
import ElementPlus from 'element-plus'
const FileSaver = require('./components/file-saver')
var JSZip = require("jszip");
import {AIMarker} from './components/ui-picture-bd-marker'
// import dynamicTable from "./components/dynamicTable.vue"

// import ElementUI from "../node_modules/element-ui/types/index"
// import 'element-ui/lib/theme-chalk/index.css';
// import Slider from './components/slider.vue';
// import { Table, TableColumn } from 'element-ui';
// import 'element-ui/lib/theme-chalk/index.css';

export default {
        components: {
        ElementPlus,
        FileSaver,
        JSZip,
        'ui-marker':AIMarker
        // Slider,
        //elTable: Table,
        //elTableColumn: TableColumn
        },
        

        data() {
            return {
                // 
                
                count_col:0,
                showMenu:false,
                curColumn:null,

                // 
                SerialNum:[1, 2],

                // 
                tableHead:[
                    {label:'Question',
                    property:'Question'
                    },
                    {label:"Reason",
                    property:"Reason"}
                    ],

                tableHead1:[
                  {label:'Question',
                  property:'Question'}
                ],

                // original videoID and seg information
                videoID_list:[''
                ],

                SegInfo_list:[''
                ],

                tableData: [
                      {Index:1, 
                      Question: 'What is...',
                      Reason:'Reason1'
                      },
                      {Index:2,
                      Question: 'How is...',
                      Reason:'Reason2'
                      },],

                  timeHead:{label:'Start-End',
                  property:'Start-End'},
                  timeValue:[[],[]],  // store start end time in form
                  timeValue_multiVideo:[{}, {}],

                  //
                  MetaType:[{
                    value:'Text Only',
                    label:'Text Only'
                  },
                  {
                    value:'Video Only',
                    label:'Video Only'
                  },
                  {
                    value:'Text and Video',
                    label:'Text and Video'
                  }],
                  MetaTypeTitle:"Query Type",
                  // TypeValue:[
                  //   {value:'Text Only'},
                  //   {value:'Video only'}]
                  TypeValue:["",""],

                  SegInfo:[], // original segment info input
                  SegNum:[],
                  Segments:[],
                  SegValue:[[],[]],
                  SegmentTitle:"Segment",
                  SegTimeValue:[], // to store time slot
                  SegTimeValue_list:{},


                  progressLength:710,
                  
                  videoUrl:[],
                  videoUrl_list:{},

                  currentRow : [],

                  imgList:[[],[]],

                  img_base64:[],
                  img_file:[],

                  storePath:'/imgStore',

                  bboxList:[[],[]],

                  bbox_base64:[],
                  bbox_file:[],

                  fileName:[],

                  Rephrase_list:[false, false], //record row rephrase or not

            }  
        },

        computed: {
            player() {
                return this.$refs.videoPlayer.player
            },
            setProgressBgStyle () {
                return {
                    // 
                    width: `${this.width + this.sliderWidth}px`
                }
            },
            setProgressStyle () {
                return {
                    width: `${this.progressWidth}px`,
                    // background: color
                }
            },

        },

        methods:{

          JsonChange(file, filelist){
                  
                  console.log(file.raw)
                  
                  // var img_base64;
                  // reader.readAsDataURL(file.raw);
                  let this_=this
                  let reader = new FileReader()
                  reader.onload = async (e) => {
                      let document = JSON.parse(e.target.result)
                      console.log(document)
                      // this_.videoUrl = document[0]['videoID']
                      this_.videoID_list = []
                      this_.videoID_list.splice(this_.videoID_list.length, 1, document[0]['videoID'])
                      this_.videoUrl_list[document[0]['videoID']] = document[0]['videoID']
                      // this_.SegInfo = this.timeprocess_inverse(document[1]['segInfo'])  // for processed json
                      this_.SegInfo = document[1]['segInfo']
                      // this_.SegTimeValue = this_.timeProcess(document[1]['segInfo'])
                      this_.SegInfo_list = []
                      this_.SegInfo_list.splice(this_.SegInfo_list.length, 1, this_.SegInfo) 
                      this_.SegTimeValue_list[document[0]['videoID']] = this_.timeProcess(document[1]['segInfo'])
                      for (var j=2;j<document.length;j++){
                        if (j>=4){
                          this_.handleEdit(j-2)
                        }
                        this_.tableData[j-2].Index = j-1
                        this_.tableData[j-2].Question = document[j]['Question']
                        this_.tableData[j-2].Reason = document[j]['Reason']
                        this_.TypeValue[j-2] = document[j]['QueryType']
                        // this_.timeValue[j-2] = this_.timeprocess_inverse(document[j]['Start_EndTime'])  // for processed json
                        if(typeof(document[j]['Start_EndTime'])=='string'){
                          this_.timeValue_multiVideo[j-2][document[0]['videoID']] = document[j]['Start_EndTime']
                        }
                        else{
                          this_.timeValue_multiVideo[j-2][document[0]['videoID']] = document[j]['Start_EndTime'][document[0]['videoID']]
                        }
                        
                      }
                  }
                  
                  let a = reader.readAsText(file.raw)
                  // console.log(a)
                  
          },

          jsonunfold(){

          },

          JsonRemove(){

          },
            
            generateSeg(){
              // console.log(this.SegInfo);
              // console.log(this.videoID_list)
              // console.log(this.SegInfo_list)

              for(var ii=0;ii<this.videoID_list.length;ii++){
                this.videoUrl = this.videoID_list[ii];
                this.SegInfo = this.SegInfo_list[ii];
                if (this.checkURL(this.videoUrl)&&this.checkSegInput(this.SegInfo))
                {
              this.Segments=[];
              // for(var i=1;i<=parseInt(this.SegNum);i++){
              //   this.Segments.push('Seg'+i)
              // }
              // console.log(this.Segments)
              var i=0; 
              var counter=0;
              var counter1=0;

              while (true){
                i=this.SegInfo.indexOf(';',i);
                if (i<0)
                  break
                // console.log(this.SegInfo.indexOf(';',i))
                counter++;
                i=i+1;
              }

              i=0;

              while (true){
                i=this.SegInfo.indexOf('-',i);
                if (i<0)
                  break
                // console.log(this.SegInfo.indexOf('-',i))
                counter1++;
                i++;
              }

              if ((counter1==counter)&&(this.SegInfo.indexOf(' ')<0))
              {
                for(var i=1;i<=counter;i++){
                this.Segments.push('Seg'+i)}

                // for(var i=1;i<=counter;i++){
                //   if (i==1){
                //     previous1 = this.SegInfo.indexOf('-', previous1+1)
                //     time_temp = this.SegInfo.substring(0, previous1)
                //   }
                  
                //   time_temp = this.SegInfo.substring(index-4, index-1)
                this.SegTimeValue = this.timeProcess(this.SegInfo) 

                }

              }
              
              else{
                // alert('Lack of -; or hava space')
                break
              }

              this.videoUrl_list[this.videoUrl]=this.videoUrl
              this.SegTimeValue_list[this.videoUrl]=this.SegTimeValue
              if (!this.timeValue_multiVideo[0].hasOwnProperty(this.videoUrl)){
                this.timeValue_multiVideo[0][this.videoUrl]=''
              }
              
              if (!this.timeValue_multiVideo[0].hasOwnProperty(this.videoUrl)){
                this.timeValue_multiVideo[1][this.videoUrl]=''
              }
              
              }

              var FileName=''
              for (var key in this.videoUrl_list){
                FileName=FileName+key+'_'
              }
              FileName=FileName.substring(0, FileName.length-1)
              this.fileName=FileName


              // console.log(this.videoUrl_list)
              // console.log(this.SegTimeValue_list)
              // console.log(this.timeValue_multiVideo)
              
            },

            checkURL(videoURL){
              if(videoURL.length!=11){
                alert('Invalid videoURL!')
                return 0
              }
              else{
              return 1
              }

            },
            
            checkSegInput(seginfo){
              var flag=1;
              if(seginfo.indexOf(" ")>0){
                alert('Space in input!Please delete all spaces');
                flag=0
              }
              var left=seginfo.indexOf('-');
              var right=seginfo.indexOf(';');
              while (right>0&right<seginfo.length-1){
                var a=seginfo.substring(left+1,right);
                var b=seginfo.substring(right+1,right+1+a.length);
                // console.log(a)
                // console.log(b)
                if (a!=b){
                  alert('Interval between segments!');
                  flag=0
                }
                left=seginfo.indexOf('-',left+1);
                right=seginfo.indexOf(';',right+1)
              }
              return flag
            },

            AddVideo(index, row){
                this.videoID_list.splice(index+1, 0, {videoID: ''}),
                this.SegInfo_list.splice(index+1, 0, {seginfo: ''})
            },

            DeleteVideo(index, row){
                this.videoID_list.splice(index, 1),
                this.SegInfo_list.splice(index, 1)
            },

            handleEdit(index, row) {
                this.tableData.splice(index+1,0,{Index:'', Question: '',
                      Reason:''
                      }),
                
                this.TypeValue.splice(index+1,0,""),
                this.SegValue.splice(index+1,0,[]),
                this.imgList.splice(index+1,0,[]),
                this.bboxList.splice(index+1,0,[]),
                // this.timeValue.splice(index+1,0,[])
                this.timeValue_multiVideo.splice(index+1,0,{})
                for(var key in this.videoUrl_list){
                  this.timeValue_multiVideo[this.timeValue_multiVideo.length-1][key]=''
                }
                // console.log(this.timeValue_multiVideo)
                this.Rephrase_list.splice(index+1,0,false)
                this.generateSerialNum()
                // console.log(this.imgList)
            },
            handleDelete(index, row) {
                this.tableData.splice(index,1),
                
                this.TypeValue.splice(index,1),
                this.SegValue.splice(index,1),
                this.imgList.splice(index,1),
                this.timeValue_multiVideo.splice(index,1),
                this.bboxList.splice(index,1)
                this.Rephrase_list.splice(index,1)
                this.generateSerialNum()
                // console.log(index);
            },

            generateSerialNum(){
              for(var i=0;i<this.tableData.length;i++){
                this.tableData[i].Index = i+1
              }
            },

            Rephrase(index, row){
              this.tableData.splice(index+1,0,{index: '', Question: '',
                      Reason:''
                      }),
              
              this.TypeValue.splice(index+1,0,this.TypeValue[index]),
              this.SegValue.splice(index+1,0,this.SegValue[index]),
              this.imgList.splice(index+1,0,this.imgList[index]),
              this.bboxList.splice(index+1,0,this.bboxList[index]),
              this.timeValue_multiVideo.splice(index+1,0,this.timeValue_multiVideo[index])
              this.Rephrase_list.splice(index+1,0,true)
              this.generateSerialNum()
            },

            tableRowClassName({ row, rowIndex }) {
                if (this.Rephrase_list[rowIndex]) {
                    return 'warning-row'
                } 
            },
            

        
            changeMetaType(event,row){
              //this.TypeValue=this.MetaType[obj].value
              // const { value, label } = params;
		          this.TypeValue[row] = event;
              // console.log(this.TypeValue[row])
            },
            changeSegValue(event,index,seg){         
              if (event)
              { 
                // console.log(typeof seg)
                var temp = this.SegValue[index];
                var flag = 0;
                var seg_num = parseInt(seg.substring(3));
                for (var i=0;i<temp.length;i++)
                {
                  if (temp[i]===seg_num)
                  {
                    flag = 1;
                    break
                  }
                }
                if (flag==0)
                {
                  this.SegValue[index].push(parseInt(seg.substring(3)))
                }             
              }
              else
              {
                var temp = this.SegValue[index];
                var seg_num = parseInt(seg.substring(3))
                for (var i=0;i<temp.length;i++)
                {
                  if (temp[i]===seg_num)
                  {
                    break
                  }
                }
                  this.SegValue[index].splice(i,1)                
              }
              // console.log(seg)
              // console.log(event)
              // console.log(this.SegValue)
            },
            ShowIntroduction(){
              this.Introduction = !this.Introduction
            },
            upload(){
              // var fs = require("fs")
              var QuestionList=[]
              var seginfo
              var left;
              var right;
              var Ques;
              var filename;
              var Counter;
              var temp=[];
              var start_endCheck=[];
              var start_endCheckflag;
              var flag_QMark;
              var FileName='';
              var img_seq=this.rephrase_q_img_match(this.Rephrase_list);
              console.log(this.Rephrase_list);
              console.log(img_seq);
              for (var key in this.videoUrl_list){
                temp.splice(temp.length, 1, this.videoUrl_list[key])
                FileName=FileName+key+'_'
              }
              FileName=FileName.substring(0, FileName.length-1)
              this.fileName=FileName
              QuestionList.push({videoID:temp})
              temp = []
              QuestionList.push({segInfo:this.SegTimeValue_list})
              // console.log(QuestionList)
              for (var i = 0; i<this.tableData.length;i++)
                { 
                  Ques = this.tableData[i].Question;
                  // console.log(Ques[Ques.length-1])
                  if (Ques[Ques.length-1]=='?'){
                    flag_QMark=1;
                    // console.log(flag_QMark)
                  }
                  else{
                    flag_QMark=0;
                    alert('Invalid Question in Q'+(i+1))
                  }
                  left = Ques.indexOf('<');
                  right = Ques.indexOf('>');
                  Counter=0;
                  filename=[];
                  var flag = 0;
                  while(left>=0){
                    temp = Ques.substring(left+1,right);
                    // console.log(temp)
                    Counter++;

                    if (temp.match('bbox')){
                      var b=temp.match('bbox');
                      b=b["index"];
                      var num=temp.substring(b,b+5);
                      if (num[num.length-1]=='/'){
                        num=num.substring(0,num.length-1)
                      }
                      b=temp.indexOf('/');
                      if (b==-1){
                        alert('lack of / in Q'+i)
                      }
                      var name=temp.substring(b+1,right);
                      Ques=Ques.substring(0,left+1)+
                           num+'/'+name+
                           Ques.substring(right)
                      if (flag==0){
                        filename = this.fileName+'_bbox_Q'+i+'.jpg';
                        flag++;
                      }
                      
                    }
                    else{
                      b=temp.indexOf('/');
                      if (b==-1){
                        alert('lack of / in Q'+i)
                      }
                      var name=temp.substring(b+1,right);
                      Ques=Ques.substring(0,left+1)+
                           name+
                           Ques.substring(right)
                      
                      if (flag==0){
                        filename = this.fileName+'_img_Q'+i+'.jpg';
                        flag++;
                      }
                    }
                    // if (type == 'i'){
                    //   Ques=Ques.substring(0,left+1)+
                    //                              this.videoUrl+'_img_Q'+i+'_'+num+'.jpg'+
                    //                              Ques.substring(right)
                    // }
                    // if (type == 'b'){
                    //   Ques=Ques.substring(0,left+1)+
                    //                              this.videoUrl+'_bbox_Q'+i+'_'+num+'.jpg'+
                    //                              Ques.substring(right)
                    // }
                    left=0;right=0;
                    for(var j=0;j<=Counter;j++){
                      left = Ques.indexOf('<',left);
                      right = Ques.indexOf('>',right);
                      left++;
                      right++;
                    }
                    left--;right--;
                  }

                  // console.log(Ques);

                  var start_endtime, start_endCheckflag, segvalue
                  this.timeValue = this.timeValue_multiVideo[i]
                  temp = {}
                  for(var key in this.timeValue){
                    if(this.timeValue[key].length>0){
                      [start_endtime, start_endCheckflag]=this.obtainStartEndTime(this.timeValue[key])
                      segvalue = this.generateSegvalue(start_endtime, key)
                      temp[key] = segvalue
                      if (start_endCheckflag==1){
                        break
                      }
                    }
                    
                  }
                  segvalue=temp
                  
                  temp = []
                  // start_endtime=this.obtainStartEndTime(this.timeValue[i]);
                  if (start_endCheckflag==1){
                    start_endCheck.push(1)
                    break
                  }
                  start_endCheck.push(0);
                  // console.log('start_endtime')
                  // console.log(start_endtime)

                  // var segvalue = this.generateSegvalue(start_endtime)
                  

                  // console.log(start_endtime);

                  filename = filename.substring(0, filename.length-5) + String(img_seq[i]) + '.jpg'

                  QuestionList.push({ID:this.fileName+'_Q'+i,
                                    Question:Ques, 
                                    // Reason:this.tableData[i].Reason, 
                                    QueryType:this.TypeValue[i], 
                                    // Segment:this.SegValue[i],
                                    Segment:segvalue,
                                    Filename:filename,
                                    Start_EndTime:start_endtime
                                    },
                                    )
                }
                
              // console.log(QuestionList)
              // store json file: 
              var sum=0
              // console.log(start_endCheck)
              for (var i=0;i<start_endCheck.length;i++){
                sum+=start_endCheck[i];
              }
              // console.log(sum)
              // console.log(flag_QMark)
              console.log(QuestionList)
              if (sum==0&&flag_QMark==1){
              var data = JSON.stringify(QuestionList)
              var blob = new Blob([data], {type: ''})
              FileSaver.saveAs(blob, this.fileName+'.json')
              }
            },

            generateSegvalue(start_endtime, key){
              var slot
              var result=[]
              var temp
              // console.log(start_endtime)
              this.SegTimeValue = this.SegTimeValue_list[key]
              
              for(var i=0;i<start_endtime.length;i++){
                  slot = start_endtime[i]
                  for(var j=0;j<this.SegTimeValue.length;j++){
                    if ((this.SegTimeValue[j][0]>=slot[0]&&this.SegTimeValue[j][1]<=slot[1])||
                        (this.SegTimeValue[j][0]<slot[1]&&this.SegTimeValue[j][1]>slot[1])||
                        (this.SegTimeValue[j][0]<slot[0]&&this.SegTimeValue[j][1]>slot[0]))
                        {
                          // temp = this.Segments[j]
                          if (result.indexOf(temp)<0){
                            result.push(j+1)
                          }
                        }
                  }
              }
              return result
            },

            rephrase_q_img_match(rephrase_list){
              var result = []
              var count = -1
              for(var i=0;i<rephrase_list.length;i++){
                if (!rephrase_list[i]){
                  count+=1
                }
                result.splice(i,0,count)
              }
              return result
